import { useRef } from 'react';
import { useSprings, animated, config } from '@react-spring/web';
import { useDrag } from '@use-gesture/react';
import clamp from 'lodash.clamp';
import swap from 'lodash-move';

import styles from '../styles.module.css';

const fn =
    (order: number[], active = false, originalIndex = 0, curIndex = 0, x = 0) =>
    (index: number) =>
        active && index === originalIndex
            ? {
                  x: curIndex * 100 + x, // Changed from y to x
                  scale: 1.1,
                  zIndex: 1,
                  shadow: 15,
                  immediate: (key: string) => key === 'zIndex',
                  config: (key: string) => (key === 'x' ? config.stiff : config.default), // Changed from y to x
              }
            : {
                  x: order.indexOf(index) * 100, // Changed from y to x
                  scale: 1,
                  zIndex: 0,
                  shadow: 1,
                  immediate: false,
              };

function DraggableList({ items }: { items: string[] }) {
    const order = useRef(items.map((_, index) => index));
    const [springs, api] = useSprings(items.length, fn(order.current));
    const bind = useDrag(({ args: [originalIndex], active, movement: [x] }) => { // Changed movement destructuring to x
        const curIndex = order.current.indexOf(originalIndex);
        const curColumn = clamp(Math.round((curIndex * 100 + x) / 100), 0, items.length - 1); // Changed from y to x and row to column
        const newOrder = swap(order.current, curIndex, curColumn);
        api.start(fn(newOrder, active, originalIndex, curIndex, x)); // Changed y to x
        if (!active) order.current = newOrder;
    });

    return (
        <div className={styles.content} style={{ width: items.length * 100, height: 100 }}> // Changed style for horizontal layout
            {springs.map(({ zIndex, shadow, x, scale }, i) => (
                <animated.div
                    {...bind(i)}
                    key={i}
                    style={{
                        zIndex,
                        boxShadow: shadow.to(s => `rgba(0, 0, 0, 0.15) 0px ${s}px ${2 * s}px 0px`),
                        x,
                        scale,
                        position: 'absolute', // Ensuring items can be positioned absolutely
                        height: '100%', // Ensure the item takes full container height
                        display: 'flex', // Aligning the content within the box
                        alignItems: 'center', // Centering content vertically
                        justifyContent: 'center' // Centering content horizontally
                    }}
                    children={items[i]}
                />
            ))}
        </div>
    );
}

export default DraggableList;
